/**
 *
 * Helpful reusable funciont-like things that will be used to implement
 * styles for components
 *
 */
.dse-width-none {
  width: 0rem;
}

.dse-height-none {
  height: 0rem;
}

.dse-width-xxxs {
  width: 0.25rem;
}

.dse-height-xxxs {
  height: 0.25rem;
}

.dse-width-xxs {
  width: 0.5rem;
}

.dse-height-xxs {
  height: 0.5rem;
}

.dse-width-xs {
  width: 0.75rem;
}

.dse-height-xs {
  height: 0.75rem;
}

.dse-width-sm {
  width: 1rem;
}

.dse-height-sm {
  height: 1rem;
}

.dse-width-md {
  width: 1.5rem;
}

.dse-height-md {
  height: 1.5rem;
}

.dse-width-lg {
  width: 2rem;
}

.dse-height-lg {
  height: 2rem;
}

.dse-width-xl {
  width: 3rem;
}

.dse-height-xl {
  height: 3rem;
}

.dse-width-xxl {
  width: 4.5rem;
}

.dse-height-xxl {
  height: 4.5rem;
}

.dse-width-xxxl {
  width: 6rem;
}

.dse-height-xxxl {
  height: 6rem;
}

.dse-text {
  margin: 0;
}

.dse-text-xs {
  font-size: 0.75rem;
}

.dse-text-sm {
  font-size: 0.875rem;
}

.dse-text-base {
  font-size: 1rem;
}

.dse-text-lg {
  font-size: 1.125rem;
}

.dse-text-xl {
  font-size: 1.5rem;
}
