/**
 *
 * Helpful reusable funciont-like things that will be used to implement
 * styles for components
 *
 */
.dse-select {
  display: flex;
  flex-direction: column;
  position: relative;
}

.dse-select__label {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.75rem;
  border: 1px solid var(--dse-form-border-color, #f1f2f3);
  background: var(--dse-form-bg-color, #fff);
  font-family: var(--dse-body-font-family, "Playfair Display", serif);
  cursor: pointer;
}

.dse-select__caret {
  transition: all 250ms ease;
}

.dse-select__caret--open {
  transform: rotate(180deg);
}

.dse-select__caret--closed {
  transform: rotate(0deg);
}

.dse-select__overlay {
  padding: 0rem;
  margin: 0rem;
  border: 1px solid var(--dse-form-border-color, #f1f2f3);
  background-color: var(--dse-form-bg-color, #fff);
  z-index: 99;
  list-style-type: none;
  position: absolute;
  width: 100%;
  box-shadow: var(--dse-shadow-base, 0px 4px 11px rgba(157, 157, 157, 0.21));
}

.dse-select__option {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  cursor: pointer;
  padding: 0.75rem;
  box-sizing: border-box;
}

.dse-select__option--highlighted {
  background-color: var(--dse-form-bg-color-hover, #f1f2f3);
}

.dse-select__option--selected {
  background-color: var(--dse-form-bg-option-selected, #5ece7b);
  color: var(--dse-form-color-option-selected, #fff);
}
